
body,ol,ul,h1,h2,h3,h4,h5,h6,p,th,td,dl,dd,form,fieldset,legend,input,textarea,select{margin:0;padding:0
}
body{font:12px;background:#fff;-webkit-text-size-adjust:100%;
}
a{color:#2d374b;text-decoration:none
}
a:hover{color:#cd0200;text-decoration:none
}
em{font-style:normal
}
li{list-style:none
}
img{border:0;vertical-align:middle
}
table{border-collapse:collapse;border-spacing:0
}
* {
    font-family: "Lucida Grande",Lucida Sans Unicode,Hiragino Sans GB,WenQuanYi Micro Hei,Microsoft YaHei,Verdana,Aril,sans-serif;
}
p{word-wrap:break-word
}
html,
  body,
  #app {
    width: 100%;
    height: 100%;
}
.bar-header {
    border-bottom-width: 1px;
    height: 44px;
    padding: 5px;
    text-align: center;
    line-height: 44px;
    position: relative;
}
.bar-header .button,.title .button{
    padding: 2px 4px 1px;
    min-width: 28px;
    min-height: 44px;
    font-size: 12px;
    line-height: 26px;
    background: url('./assets/left.png') no-repeat center center;
    position: absolute;
    left: 0;
}
.title {
    background: #fff;
    height: 44px;
    width: 100%;
    text-align: center;
    font-size: 16px;
    line-height: 44px;
    position: relative;
}



